.header {
    display: grid;
    align-items: center;
    border-bottom: 1px solid #0000007a;
    background-color: $obscure;
    font-size: 200%;
    padding: 10px 20px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 10;

    .header-container {
    display: grid;
    justify-content: center;
    grid-auto-flow: column;
    gap: 25px;

    a {
        color: $white;
    }

    a:hover {
        color: $greenFusion;
    }

    .active-link {
        color: $greenFusion !important;
        font-weight: 700;
    }
}
}





