.new-character {
    padding: 2rem;
    background-color: #1e1e1e;
    color: #fff;
    min-height: 100vh;
    padding-top: 100px;
  
    h2 {
      font-size: 2rem;
      color: $greenFusion;
      margin-bottom: 1rem;
      text-align: center;
    }
  
    .form {
      display: flex;
      flex-direction: column;
      max-width: 500px;
      margin: 0 auto;
      background-color: #2c2c2c;
      padding: 2rem;
      border-radius: 1rem;
      box-shadow: 0 0 20px rgba(0, 255, 149, 0.1);
  
      .message-error {
        color: #ff4d4d;
        font-weight: bold;
        text-align: center;
        margin-bottom: 1rem;
      }
  
      label {
        margin-top: 1rem;
        margin-bottom: 0.5rem;
        font-weight: bold;
      }
  
      input,
      select {
        padding: 0.7rem;
        border-radius: 0.5rem;
        border: 1px solid #00ff95;
        background-color: #1e1e1e;
        color: #fff;
        outline: none;
        transition: border 0.3s;
  
        &:focus {
          border-color: #00ffaa;
        }
      }
  
      button {
        margin-top: 2rem;
        padding: 0.8rem 1rem;
        background-color: #00ff95;
        border: none;
        border-radius: 0.5rem;
        color: #1e1e1e;
        font-weight: bold;
        cursor: pointer;
        transition: background-color 0.3s;
  
        &:hover {
          background-color: #00ffaa;
        }
      }
    }
  
    .image-preview {
      margin-top: 2rem;
      text-align: center;
  
      p {
        color: #00ff95;
        margin-bottom: 1rem;
      }
  
      img {
        max-width: 300px;
        width: 100%;
        border-radius: 1rem;
        box-shadow: 0 0 15px rgba(0, 255, 149, 0.3);
      }
    }
  }